 0;\r\n    padding: 0;\r\n}\r\n\r\n@mixin flex-basic ($align-dir: center, $justify-dir: start, $flex-wrap: nowrap) {\r\n    display: flex;\r\n    align-items: $align-dir;\r\n    justify-content: $justify-dir;\r\n    flex-wrap: $flex-wrap;\r\n}\r\n\r\nhtml {\r\n    scroll-behavior: smooth;\r\n}\r\n\r\nbody {\r\n    font-family: $font-primary;\r\n    @include clear-css();\r\n    background-color: $background-primary;\r\n    color: $text-color-primary;\r\n    position: relative;\r\n}\r\n\r\n.skip-link {\r\n    position: absolute;\r\n    left: 0;\r\n    top: -40px;\r\n    background-color: $background-secondary;\r\n    color: white;\r\n    padding: 8px;\r\n    z-index: 100;\r\n    min-width: 44px;\r\n    min-height: 44px;\r\n}\r\n\r\n.skip-link:focus {\r\n    top: 5em;\r\n}\r\n\r\nheader {\r\n    .container-navbar {\r\n        @include flex-basic(center, space-between);\r\n        background-color: $background-secondary;\r\n        box-shadow: rgba(15, 15, 16, 0.2) 0px 7px 29px 0px;\r\n        height: 4em;\r\n        width: 100%;\r\n        position: fixed;\r\n        z-index: 999;\r\n\r\n        heading-app {\r\n\r\n            .icon {\r\n                a {\r\n                    @include flex-basic();\r\n                    text-decoration: none;\r\n                    color: $text-color-secondary;\r\n\r\n                    img {\r\n                        width: 50px;\r\n                        height: 50px;\r\n                        margin-left: 3em;\r\n                    }\r\n\r\n                    p {\r\n                        font-weight: bold;\r\n                        font-size: 1.5rem;\r\n                        width: 300px;\r\n                    }\r\n                }\r\n            }\r\n        }\r\n\r\n        nav-bar-app {\r\n\r\n            #hamburger {\r\n                all: unset;\r\n                font-size: 1.5em;\r\n                color: $text-color-secondary;\r\n                text-decoration: none;\r\n                display: none;\r\n                padding: 20px 15px;\r\n            }\r\n\r\n            nav {\r\n                .item-list {\r\n                    @include flex-basic(center, space-evenly);\r\n                    margin-right: 5em;\r\n                    gap: 4em;\r\n\r\n                    .nav-item {\r\n                        list-style: none;\r\n\r\n                        a {\r\n                            min-height: 44px;\r\n                            min-width: 44px;\r\n                            text-decoration: none;\r\n                            font-size: 1.2rem;\r\n                            font-weight: bold;\r\n                            color: $text-color-secondary;\r\n                            padding: 10px 5px;\r\n                            border-radius: 10px;\r\n                            transition: .5s;\r\n                        }\r\n\r\n                        a:hover {\r\n                            text-decoration: underline;\r\n                            background-color: $background-primary;\r\n                            color: $text-color-tertiary;\r\n                            padding: 5px 10px;\r\n                        }\r\n                    }\r\n                }\r\n            }\r\n        }\r\n    }\r\n\r\n    hero-app {\r\n        .hero-element {\r\n            width: 100%;\r\n            height: 70vh;\r\n            position: relative;\r\n\r\n            picture {\r\n                width: 100%;\r\n                height: 100%;\r\n\r\n                img {\r\n                    width: 100%;\r\n                    height: 100%;\r\n                    object-fit: cover;\r\n                }\r\n            }\r\n\r\n            p {\r\n                position: absolute;\r\n                top: 30%;\r\n                color: $text-color-secondary;\r\n                font-weight: bold;\r\n                font-size: 2.5rem;\r\n                text-align: center;\r\n                line-height: 3em;\r\n                text-shadow: -15px 5px 20px #ced0d3;\r\n                background-color: rgba(0, 0, 0, 0.573);\r\n                filter: opacity(0.7);\r\n                width: 100%;\r\n                height: 3em;\r\n            }\r\n        }\r\n    }\r\n}\r\n\r\n.restaurant-item__not__found {\r\n    font-size: 1rem;\r\n    font-weight: bold;\r\n    text-align: center;\r\n    padding: 2rem;\r\n    position: absolute;\r\n    left: 50%;\r\n    transform: translate(-50%, -50%);\r\n}\r\n\r\nmain {\r\n    padding-top: 6em;\r\n\r\n    .container-about {\r\n        display: grid;\r\n        grid-template-columns: 1fr 1fr;\r\n        width: 80%;\r\n        margin: 0 auto;\r\n\r\n        img {\r\n            width: 90%;\r\n            height: 100%;\r\n            box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;\r\n        }\r\n\r\n        .about-description {\r\n            h1 {\r\n                color: $text-color-tertiary;\r\n            }\r\n\r\n            p {\r\n                font-size: 1rem;\r\n            }\r\n        }\r\n    }\r\n\r\n    .advantage {\r\n        @include flex-basic(center, center, nowrap);\r\n        width: 80%;\r\n        margin: 5em auto;\r\n        gap: 2em;\r\n\r\n        .advantage-card {\r\n            border-radius: 20px;\r\n            flex-grow: 1;\r\n            text-align: center;\r\n            width: 5em;\r\n            padding: 1em;\r\n            box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;\r\n\r\n            img {\r\n                width: 10em;\r\n            }\r\n\r\n            .card-caption {\r\n                border-radius: 20px;\r\n            }\r\n\r\n            h2 {\r\n                font-size: 1.2rem;\r\n            }\r\n\r\n            p {\r\n                font-size: .9rem;\r\n            }\r\n        }\r\n    }\r\n\r\n    h2.explore {\r\n        color: $text-color-tertiary;\r\n        text-align: center;\r\n        margin-top: 1em;\r\n        margin-bottom: 2em;\r\n    }\r\n\r\n    // Item Restaurant\r\n    .restaurant-list {\r\n        display: grid;\r\n        grid-template-columns: repeat(3, 1fr);\r\n        gap: 3em 2em;\r\n        margin: 0 3em;\r\n\r\n        .restaurant-item {\r\n            box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;\r\n            position: relative;\r\n            border-radius: 20px;\r\n            padding-bottom: 2em;\r\n            transition: .5s;\r\n\r\n            .thumb {\r\n                img {\r\n                    width: 100%;\r\n                    border-top-right-radius: 20px;\r\n                    border-top-left-radius: 20px;\r\n                }\r\n\r\n                p.city {\r\n                    background-color: #0000005f;\r\n                    color: $text-color-secondary;\r\n                    font-weight: bold;\r\n                    font-size: 1rem;\r\n                    margin-left: 0;\r\n                    position: absolute;\r\n                    top: 5px;\r\n                    padding: 12px 24px;\r\n                    border-top-right-radius: 20px;\r\n                    border-bottom-right-radius: 20px;\r\n                }\r\n            }\r\n\r\n            .description {\r\n                margin-left: 2em;\r\n                margin-right: 2em;\r\n\r\n\r\n                p.rate {\r\n                    font-weight: bold;\r\n                    font-size: .8rem;\r\n                }\r\n\r\n                h3.restaurant__title>a {\r\n                    font-size: 1.3rem;\r\n                    color: $text-color-tertiary;\r\n                    text-decoration: none;\r\n                    padding: 1em 0;\r\n                    min-width: 44px;\r\n                    min-height: 44px;\r\n                }\r\n\r\n                p.desc {\r\n                    font-size: .9rem;\r\n                }\r\n            }\r\n        }\r\n\r\n        .restaurant-item:hover {\r\n            transform: scale(1.05);\r\n        }\r\n    }\r\n\r\n    // Detail Restaurant\r\n    .restaurant-detail {\r\n        width: 80vw;\r\n        margin: 